import * as React from 'react';

export default class ChatHistory extends React.Component {
  render() {
    return (
        <ul className="collection">
            <li className="collection-item avatar">
                <img src="https://www.inspirama.co/wp-content/uploads/2017/08/jeff-bezos.jpg" alt="jeff_bezos" className="circle"/>
                <span className="title">Jeff Bezos</span>
                <p>
                    <i className="prefix mdi-action-alarm" />
                    <span className="message-date">25.09.2018 at 07:45</span>
                    <br/>
                    <p className="message-content">Good morning, Daniel! Nice to see you... Well, I have many questiong to your about the next meeting, which you organize at tomorrow... So, did you completed all preparings ?</p>
                </p>
            </li>
        </ul>
    );
  }
}




